<template>
  <v-app id="app">
    
    <!-- unit-data ref="userData"></unit-data -->

    <router-view v-if="$store.state.chronus"/>
    <div v-else>

      <chronus ref="chronusREF" v-if="$store.state.token"></chronus>
      
      <chronus-now v-else></chronus-now>
    </div>
  </v-app>
</template>

<script>
import ChronusNow from './components/Conning/ChronusNow.vue'
import Init from './components/Data/Init.vue'
import Loading from './components/Data/Loading.vue'
import UnitData from './components/Data/UnitData.vue'
import Chronus from './pages/Home/Chronus/Chronus.vue'

export default {
    components: { Init, UnitData, Chronus,
      Loading,
        ChronusNow 
    },
    name: 'App',
    created() {
      if (sessionStorage.getItem('store')) {
        // 存儲狀態
        this.$store.replaceState(Object.assign({}, this.$store.state, JSON.parse(sessionStorage.getItem('store'))))
      }
      // 在頁面刷新時將vuex裏的信息保存到sessionStorage裏
      window.addEventListener('beforeunload', () => {
        // session存儲
        sessionStorage.setItem('store', JSON.stringify(this.$store.state))
      })
    },
    mounted() {

        const needAuth = this.$store.getters.checkToken()
        const needLogin = this.$store.getters.checkUserBackend()
        console.log(needAuth, needLogin)
        /*
        if (needAuth) {
          this.$store.commit('changeAuth', null)
          this.$store.commit('saveBackendUser', '')
          this.$router.push({ path: '/login' })
        }

        if (needLogin) {
          this.$store.commit('changeAuth', null)
          this.$store.commit('saveBackendUser', '')
          this.$router.push({ path: '/login' })
        }
        */
         
        if (!needLogin == true && !needAuth == true) {

          this.initialize(!needAuth)
        } else {
          this.$store.commit('changeAuth', null)
          this.$store.commit('saveBackendUser', '')
          this.$router.push({ path: '/login' })
        }
        
        console.log('完畢 》》》》》')
    },
    methods: {
      
      async initLoading() {
        await this.$refs.userData.init()

        // await this.$refs.initREF.init()
      },
      async gotAuth(needInit) {
          const res = await this.conn.token()
          
          this.$store.commit('changeAuth', res.jwt, res.user)

          if (needInit) {
            this.initialize(needInit)
          }
      },
      async initialize(hasToken) {
        console.log('进来了', hasToken)
        let chronu = this.$store.getters.checkChronus()

        if (chronu) {
          chronu = await this.chronusNow()
          this.$store.commit('saveChronus', [ chronu, true ])
        }

        if (hasToken) {
          // this.initLoading()
        }
      },

      async chronusNow() {

          let res

          let condition = {
              '_where[start_lte]': this.view.getToday(),
              '_where[end_gte]': this.view.getToday()
          }

          res = await this.conn.get(
              this.api.chronus,
              this.$store.state.token,
              condition
          )

          console.log('搜索 時光 =', res)
          if (res) {
              if (res.length > 0) {
                  return res[0] 
              }
          }

          this.view.alert_message('新的結算日程開始了，但是數據庫未有關於新日程的時間準備，請移步後臺新增時間代號。')
          return ''
      }
    }
}
</script>

<style lang="sass">
@import "@/common/sass/base.sass"
</style>



