
const Account = () => import('@/pages/Account/Account')
const AccountResult = () => import('@/pages/Account/Result/AccountResult')
const AccountStatement = () => import('@/pages/Account/Statement/AccountStatement')

const UserCenter = () => import('@/pages/Account/Center/UserCenter')

const AccountTest = () => import('@/pages/Account/Test/AccountTest')

export default {
    
    path: '/home/account',
    component: Account,
    
    redirect:'/home/account/statement',
    children: [
        {
            path: '/home/account/result',
            component: AccountResult,
        },
        {
            path: '/home/account/user_center',
            component: UserCenter
        },
        {
            path: '/home/account/statement',
            component: AccountStatement
        },

        {
            path: '/home/account/test',
            component: AccountTest
        }
    ]
}


